.agitation {
  padding: 50px 40px;
}
.agitation__text {
  color: #283645;
  text-align: center;
  line-height: 30px;
  margin: 0;
}
.comment {
  color: #283645;
  line-height: 24px;
  padding-top: 25px;
  min-height: 155px;
}
.comment__user-name {
  font-weight: bold;
  margin: 0;
}
.comment__time {
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 300;
}
.download {
  color: #ffffff;
  background-color: #1d2631;
}
.download__button {
  font-size: 18px;
  line-height: 18px;
}
.download__availability {
  line-height: 24px;
  padding-bottom: 34px;
  margin: 0;
}
.download__link {
  padding-bottom: 32px;
}
.download__logo {
  padding-bottom: 28px;
}
.download__agitation {
  margin: 0;
}
.download__image {
  min-height: 490px;
  background: url("../img/bg-back-mobile@1x.jpg") no-repeat;
}
.download__image-2 {
  background: url("../img/iphone-hand-mobile@1x.png") no-repeat;
  height: 290px;
}
@media (min-width: 660px) {
  .download {
    min-height: 620px;
    background-image: url("../img/bg-back-tablet@1x.jpg");
  }
  .download__wrapper {
    display: flex;
    flex-direction: row-reverse;
    justify-content: flex-end;
    padding-left: 20px;
    padding-top: 100px;
    height: 620px;
    background: url("../img/iphone-hand-tablet@1x.png") no-repeat 0 100%;
    box-sizing: border-box;
  }
  .download__column {
    width: 300px;
  }
}
@media (min-width: 1200px) {
  .download {
    background-image: url("../img/bg-back-tablet@1x.jpg");
  }
}
@font-face {
  font-family: "Open Sans";
  src: url("../../fonts/opensans.woff") format("woff"), url("../../fonts/opensansbold.woff") format("woff"), url("../../fonts/opensanslight.woff") format("woff"), url("../../fonts/opensans.woff2") format("woff2"), url("../../fonts/opensansbold.woff2") format("woff2"), url("../../fonts/opensanslight.woff2") format("woff2");
}
.logo-wrapper {
  height: 65px;
}
.main-nav {
  background-color: #283645;
}
@media (min-width: 660px) {
  .main-nav__toggle {
    display: none;
  }
}
@media (min-width: 1200px) {
  .main-nav__list {
    display: flex;
  }
  .main-nav {
    min-width: 1200px;
    display: flex;
    background-color: #0f151b;
  }
  .main-nav__wrapper {
    padding-left: 160px;
  }
}
.offer__opportunity {
  background-color: #f2f2f2;
  padding-top: 50px;
}
.offer__question {
  padding-top: 46px;
  padding-bottom: 37px;
  margin: 0;
  font-size: 24px;
  color: #283645;
}
.offer__slogan {
  margin: 0;
  padding-bottom: 50px;
  font-size: 24px;
  color: #283645;
}
.offer__image {
  text-align: center;
}
.offer__header {
  text-align: center;
}
.offer__opportunity--qwerty {
  display: none;
}
@media (min-width: 660px) {
  .offer__opportunity {
    display: flex;
  }
  .offer__oportunity-wrapper {
    display: flex;
    justify-content: space-between;
    width: 620px;
    margin-right: 20px;
  }
  .offer__question,
  .offer__slogan {
    font-size: 40px;
  }
  .offer__oportunity-image {
    height: 280px;
    width: 300px;
    background: url("../img/icon-timer.svg") no-repeat;
  }
  .offer__opportunity--qwerty {
    background: #ffffff;
  }
}
@media (min-width: 1200px) {
  .offer__opportunity {
    margin: 0 auto;
  }
  .offer__oportunity-wrapper {
    width: 940px;
    margin: 0 auto;
  }
  .offer__item {
    display: flex;
  }
  .offer__opportunity--qwerty {
    background-color: #f2f2f2;
  }
  .offer__item-image-1::before {
    content: ' ';
    display: block;
    width: 300px;
    height: 350px;
    left: 50%;
    top: 50px;
    background: url("../img/iphone-screen-1-desktop@1x.png") no-repeat;
  }
  .offer__item-image-2::before {
    content: ' ';
    display: block;
    width: 300px;
    height: 350px;
    left: 50%;
    top: 50px;
    background: url("../img/iphone-screen-2-desktop@1x.png") no-repeat;
  }
  .offer__item-image-3::before {
    content: ' ';
    display: block;
    width: 300px;
    height: 350px;
    left: 50%;
    top: 50px;
    background: url("../img/iphone-screen-3-desktop@1x.png") no-repeat;
  }
  .offer__image {
    display: none;
  }
}
.opportunity__item-head {
  margin: 0;
  padding-bottom: 22px;
  font-size: 18px;
  line-height: 24px;
  font-weight: bold;
  text-align: center;
  color: #283645;
}
.opportunity__item-body {
  margin: 0;
  padding-bottom: 40px;
  text-align: center;
  color: #283645;
}
.opportunity .opportunity__link {
  color: pink;
}
@media (min-width: 660px) {
  .opportunity {
    width: 300px;
  }
  .opportunity__item-body {
    text-align: left;
  }
  .opportunity__item-head {
    text-align: left;
  }
}
@media (min-width: 1200px) {
  .opportunity--qwerty {
    width: 570px;
  }
}
body {
  font-size: 14px;
  font-family: "Open Sans", "Arial", sans-serif;
  line-height: 24px;
}
a {
  color: #ffffff;
  text-decoration: none;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page-footer {
  padding-top: 40px;
  max-width: 320px;
  margin: 0 auto;
}
.page-footer__logo {
  background: green;
  width: 150px;
  height: 40px;
  margin-bottom: 40px;
}
.page-footer__social-links {
  width: 150px;
  height: 40px;
  background: pink;
  margin-bottom: 43px;
}
.page-footer__develop {
  width: 150px;
  height: 40px;
  background-color: blue;
}
.page-footer__develop-link {
  color: #283645;
  line-height: 18px;
}
@media (min-width: 660px) {
  .page-footer {
    max-width: 620px;
  }
  .page-footer__wrapper {
    display: flex;
    justify-content: space-between;
  }
}
@media (min-width: 1200px) {
  .page-footer {
    max-width: 940px;
  }
}
.page-header {
  margin: 0 auto;
  background-color: #1d2632;
}
.page-header__title {
  color: #ffffff;
  font-size: 24px;
  font-weight: 300;
  margin-top: 0;
  padding-top: 35px;
  text-align: center;
}
@media (min-width: 660px) {
  .page-header {
    max-width: 660px;
  }
}
@media (min-width: 1200px) {
  .page-header {
    max-width: 1200px;
  }
}
.page-main {
  margin: 0 auto;
}
.page-main .page-main__logo-image--panorama {
  width: 100%;
}
.page-main .page-main__wrapper {
  max-width: 280px;
  margin: 0 auto;
}
@media (min-width: 660px) {
  .page-main {
    max-width: 660px;
    margin: 0 auto;
  }
  .page-main .page-main__wrapper {
    display: flex;
    flex-wrap: wrap;
    max-width: 620px;
    justify-content: space-between;
  }
}
@media (min-width: 1200px) {
  .page-main {
    max-width: 1200px;
  }
  .page-main .page-main__wrapper {
    max-width: 940px;
  }
}
.photo__wrapper-image {
  background-image: url("../img/bg-back-mobile@1x.jpg");
  background-color: #2b3b4b;
  background-repeat: no-repeat;
  height: 170px;
}
@media (min-width: 660px) {
  .photo__wrapper-image {
    width: 660px;
    height: 320px;
    background: url("../img/bg-back-tablet@1x.jpg");
  }
}
@media (min-width: 1200px) {
  .photo__wrapper-image {
    width: 1200px;
    height: 390px;
    background: url("../img/bg-back-desktop@1x.jpg");
  }
}
.site-list {
  padding: 0;
  margin: 0;
}
.site-list__item {
  text-transform: uppercase;
  font-size: 18px;
  list-style: none;
  text-align: center;
  padding: 25px;
  border-bottom: 1px solid #000001;
  border-top: 1px solid #485461;
}
@media (min-width: 1200px) {
  .site-list__item {
    border: none;
  }
}
.user-photo {
  width: 280px;
  margin-bottom: 25px;
}
.user-photo__comment {
  background-color: #f2f2f2;
}
.user-photo__name {
  text-transform: uppercase;
  background-color: #d22856;
  color: #ffffff;
}
.user-photo--panorama {
  width: 100%;
  margin-bottom: 50px;
}
.user-photo__comment-panorama {
  width: 280px;
  margin: 0 auto;
}
@media (min-width: 660px) {
  .user-photo {
    width: 300px;
  }
  .user-photo .user-photo__comment-panorama {
    min-width: 620px;
    margin: 0 auto;
  }
  .user-photo--panorama {
    width: 100%;
  }
}
@media (min-width: 1200px) {
  .user-photo .user-photo__comment-panorama {
    min-width: 940px;
  }
  .user-photo--panorama {
    width: 100%;
  }
}
